import { supabase } from './supabase'

const API_BASE_URL = 'http://localhost:8080'

async function getAuthHeaders() {
    const { data: { session } } = await supabase.auth.getSession()
    if (!session?.access_token) {
        throw new Error('No authentication token available')
    }

    return {
        'Authorization': `Bearer ${session.access_token}`,
        'Content-Type': 'application/json',
    }
}

export interface Habit {
    id: number
    name: string
    description?: string
    color: string
    userId: string
    createdAt: string
    updatedAt: string
}

export interface DailyCompletion {
    id: number
    habitId: number
    userId: string
    completedDate: string
    createdAt: string
}

// Habit API functions
export async function fetchHabits(): Promise<Habit[]> {
    const headers = await getAuthHeaders()
    const response = await fetch(`${API_BASE_URL}/habits`, { headers })

    if (!response.ok) {
        throw new Error('Failed to fetch habits')
    }

    return response.json()
}

export async function createHabit(habit: Omit<Habit, 'id' | 'createdAt' | 'updatedAt'>): Promise<Habit> {
    const headers = await getAuthHeaders()
    const response = await fetch(`${API_BASE_URL}/habits`, {
        method: 'POST',
        headers,
        body: JSON.stringify(habit),
    })

    if (!response.ok) {
        throw new Error('Failed to create habit')
    }

    return response.json()
}

export async function updateHabit(id: number, habit: Partial<Habit>): Promise<Habit> {
    const headers = await getAuthHeaders()
    const response = await fetch(`${API_BASE_URL}/habits/${id}`, {
        method: 'PUT',
        headers,
        body: JSON.stringify(habit),
    })

    if (!response.ok) {
        throw new Error('Failed to update habit')
    }

    return response.json()
}

export async function deleteHabit(id: number): Promise<void> {
    const headers = await getAuthHeaders()
    const response = await fetch(`${API_BASE_URL}/habits/${id}`, {
        method: 'DELETE',
        headers,
    })

    if (!response.ok) {
        throw new Error('Failed to delete habit')
    }
}

// Daily completion API functions
export async function toggleHabitCompletion(habitId: number, date: string): Promise<DailyCompletion | null> {
    const headers = await getAuthHeaders()
    const response = await fetch(`${API_BASE_URL}/daily-completions/toggle`, {
        method: 'POST',
        headers,
        body: JSON.stringify({ habitId, date }),
    })

    if (!response.ok) {
        throw new Error('Failed to toggle habit completion')
    }

    const text = await response.text()
    return text ? JSON.parse(text) : null
}

export async function checkServerHealth(): Promise<boolean> {
    try {
        const response = await fetch(`${API_BASE_URL}/actuator/health`, {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
        })
        return response.ok
    } catch {
        return false
    }
}