'use client'

import { useState, useEffect } from 'react'
import { useAuth } from '@/contexts/AuthContext'
import { fetchHabits, createHabit, type Habit } from '@/lib/api'
import HabitItem from './HabitItem'
import HabitForm from './HabitForm'
import { Button } from '@/components/ui/button'
import { Plus } from 'lucide-react'

export default function HabitList() {
    const { user } = useAuth()
    const [habits, setHabits] = useState<Habit[]>([])
    const [loading, setLoading] = useState(true)
    const [error, setError] = useState('')
    const [showForm, setShowForm] = useState(false)

    useEffect(() => {
        loadHabits()
    }, [])

    const loadHabits = async () => {
        try {
            setLoading(true)
            const data = await fetchHabits()
            setHabits(data)
            setError('')
        } catch (err) {
            setError('Failed to load habits')
            console.error('Error loading habits:', err)
        } finally {
            setLoading(false)
        }
    }

    const handleCreateHabit = async (habitData: Omit<Habit, 'id' | 'createdAt' | 'updatedAt'>) => {
        try {
            const newHabit = await createHabit({
                ...habitData,
                userId: user?.id || '',
            })
            setHabits(prev => [...prev, newHabit])
            setShowForm(false)
        } catch (err) {
            console.error('Error creating habit:', err)
            setError('Failed to create habit')
        }
    }

    const handleHabitUpdate = (updatedHabit: Habit) => {
        setHabits(prev => prev.map(habit =>
            habit.id === updatedHabit.id ? updatedHabit : habit
        ))
    }

    const handleHabitDelete = (habitId: number) => {
        setHabits(prev => prev.filter(habit => habit.id !== habitId))
    }

    if (loading) {
        return (
            <div className="flex justify-center items-center py-8">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
            </div>
        )
    }

    return (
        <div className="space-y-6">
            <div className="flex justify-between items-center">
                <h2 className="text-xl font-semibold text-gray-900">Your Habits</h2>
                <Button onClick={() => setShowForm(true)} className="flex items-center space-x-2">
                    <Plus className="w-4 h-4" />
                    <span>Add Habit</span>
                </Button>
            </div>

            {error && (
                <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded">
                    {error}
                </div>
            )}

            {showForm && (
                <HabitForm
                    onSubmit={handleCreateHabit}
                    onCancel={() => setShowForm(false)}
                />
            )}

            {habits.length === 0 ? (
                <div className="text-center py-8 text-gray-500">
                    <p>No habits yet. Create your first habit to get started!</p>
                </div>
            ) : (
                <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
                    {habits.map(habit => (
                        <HabitItem
                            key={habit.id}
                            habit={habit}
                            onUpdate={handleHabitUpdate}
                            onDelete={handleHabitDelete}
                        />
                    ))}
                </div>
            )}
        </div>
    )
}